











































import mixpanel from 'mixpanel-browser'
import Vue from 'vue'

export default Vue.extend({
  computed: {
    infos() {
      return [
        {
          icon: 'education',
          title: 'title.education',
          items: 'education',
        },
        {
          icon: 'language',
          title: 'title.languages',
          items: 'languages',
        },
      ]
    },
  },
  methods: {
    onDownload() {
      mixpanel.track('CV-download', {
        lang: this.$i18n.locale,
      })
    },
  },
})
