

















import Vue from 'vue'
export default Vue.extend({
  computed: {
    experiences() {
      return [
        {
          icon: 'work',
          title: 'title.experience',
          items: 'experience',
        },
        {
          icon: 'microphone',
          title: 'title.presentations',
          items: 'presentations',
        },
        {
          icon: 'rocket',
          title: 'title.projects',
          items: 'projects',
        },
      ]
    },
  },
})
